import ListItemText from '@material-ui/core/ListItemText';

const DRSRow = ({ drs, drsInfo }) => {

    const [open, setOpen] = React.useState(false);
    const rowInfo = drsInfo[drs]

    const {
        total_de_leitos,
      ocupacao_de_leitos,
      internacoes,
      obitos,
      casos,
      fase,
    } = rowInfo
    
    const leitos_por_100k = total_de_leitos['leitos_por_100k'] !== 'NaN' ? Math.round(total_de_leitos['leitos_por_100k']*10)/10 : '-'

    const ocupacao_uti = ocupacao_de_leitos['ocupacao_leitos_uti'] !== 'NaN' ? `${Math.round(ocupacao_de_leitos['ocupacao_leitos_uti']*10)/10}%` : '-'
    
    const var_casos = casos['var'] !== 'NaN' ? `${Math.round((casos['var']-1)*100)}%` : '-'
    
    const var_obitos = obitos['var'] !== 'NaN' ? `${Math.round((obitos['var']-1)*100)}%` : '-'
    
    const var_internacoes = internacoes['var'] !== 'NaN' ? `${Math.round((internacoes['var']-1)*100)}%` : '-'
    
    const fase_final = fase['fase_final']

    const itemColor = () => {

      let color = "white";

      if (fase_final === 1) {
        color = "red";
      } else if (fase_final === 2) {
        color = "orange";
      } else if (fase_final === 3) {
        color = "yellow";
      } else if (fase_final === 4) {
        color = "green";
      } else if (fase_final === 5) {
        color = "blue";
      }

      return color
    }

    return (
        <ListItem button style={{ backgroundColor: itemColor()}} onClick={() => setOpen(!open)}>
          <IconButton size="small" style={{ outline:"none"}}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
          <Box mr="auto" align="left">{drs}</Box>
          <Box pl={3} align="right">{fase_final}</Box>
          
          </ ListItem>

    )
  }

